<template>
  <div class="modal-join">
    <div class="pt-4 pb-4 text-center">
      <h1 class="font-parisienne color-point">Publessing</h1>
      <h3>Create account</h3>
    </div>
    <div class="form mt-2">
      <template v-if="state.step <= 2">
        <div class="mb-4">
          <label for="email">Email</label>
          <input type="email" class="form-control" id="email" v-model="state.account.email" placeholder="name@publessing.io" autocomplete="off" @keypress.enter="send()" ref="emailRef" :disabled="state.loading || state.step !== 1" />
        </div>
      </template>
      <template v-if="state.step === 2">
        <div class="mb-4">
          <label for="code">Code</label>
          <input type="text" class="form-control text-center" id="code" v-model="state.account.code" @keyup="keyup($event)" @keypress.enter="verify()" autocomplete="off" maxlength="6" :disabled="state.loading" />
        </div>
      </template>
      <template v-else-if="state.step === 3">
        <div class="mb-4">
          <label for="password">Password</label>
          <input type="password" class="form-control" id="password" v-model="state.account.password" @keypress.enter="create()" :disabled="state.loading" />
        </div>
        <div class="mb-4">
          <label for="confirmPassword">Confirm Password</label>
          <input type="password" class="form-control" id="confirmPassword" v-model="state.account.confirmPassword" @keypress.enter="create()" :disabled="state.loading" />
        </div>
      </template>
      <div class="actions">
        <button class="btn btn-lg btn-block btn-point no-radius" type="button" v-if="state.loading" disabled>Wait a moment ...</button>
        <button class="btn btn-lg btn-block btn-point no-radius" type="button" @click="send()" v-else-if="state.step === 1">Send verification code</button>
        <button class="btn btn-lg btn-block btn-point no-radius" type="button" @click="verify()" v-else-if="state.step === 2">Verify Email</button>
        <button class="btn btn-lg btn-block btn-point no-radius" type="button" @click="create()" v-else-if="state.step === 3">Create Account</button>
        <div class="prev" v-if="state.step > 1">
          <a @click="state.step -= 1">Prev step</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { nextTick, onMounted, reactive, ref } from "vue";
import commLib from "../libs/commonLib";
import httpLib from "../libs/httpLib";
export default {
  props: {
    callback: Function,
  },
  setup(props) {
    const state = reactive({
      step: 1,
      loading: false,
      account: {
        email: "",
        code: "",
        password: "",
        confirmPassword: "",
      },
    });

    const emailRef = ref(null);

    const keyup = (e) => {
      if ((e.target.value && e.target.value.length === 6 && e.keyCode >= 48 && e.keyCode <= 57) || (e.keyCode >= 96 && e.keyCode <= 105)) {
        verify();
      }
    };

    const send = () => {
      if (!state.account.email) {
        commLib.message.show("warning", "  .");
        document.getElementById("email").focus();
      } else if (!commLib.isValidEmail(state.account.email)) {
        commLib.message.show("warning", "    .");
        document.getElementById("email").select();
      } else {
        state.loading = true;

        httpLib
          .post("/api/account/send", { email: state.account.email })
          .then(() => {
            state.loading = false;
            commLib.message.show("success", "    .     .");
            state.step = 2;
            nextTick(() => {
              document.getElementById("code").focus();
            });
          })
          .catch((err) => {
            state.loading = false;

            switch (err.response?.status) {
              case 404:
                commLib.message.show("warning", "   .");
                break;

              case 409:
                commLib.message.show("warning", "   .");
                document.getElementById("email").focus();
                break;

              case 429:
                commLib.message.show("warning", "   .    .");
                break;

              default:
                commLib.message.show("danger", " .");
                break;
            }
          });
      }
    };

    const verify = () => {
      const args = {
        email: state.account.email,
        code: state.account.code,
      };

      if (!args.code) {
        commLib.message.show("warning", "  .");
        document.getElementById("code").focus();
        return;
      }

      state.loading = true;

      httpLib
        .post("/api/account/verify", args)
        .then(() => {
          state.loading = false;
          commLib.message.show("success", "  .  .");
          state.step = 3;

          nextTick(() => {
            document.getElementById("password").focus();
          });
        })
        .catch((err) => {
          state.loading = false;

          switch (err.response?.status) {
            case 403:
              commLib.message.show("warning", "    .");
              break;

            case 404:
              commLib.message.show("warning", "   .");
              nextTick(() => {
                document.getElementById("code").focus();
              });
              break;

            default:
              commLib.message.show("danger", " .");
              break;
          }
        });
    };

    const create = () => {
      if (!state.account.password) {
        commLib.message.show("warning", " .");
        document.getElementById("password").focus();
      } else if (!state.account.confirmPassword) {
        commLib.message.show("warning", "    .");
        document.getElementById("confirmPassword").focus();
      } else if (state.account.password !== state.account.confirmPassword) {
        commLib.message.show("warning", "    .");
        document.getElementById("confirmPassword").select();
      } else if (state.account.password.length < 8 || state.account.password.length > 21) {
        commLib.message.show("warning", " 8  21  .");
        document.getElementById("password").select();
      } else {
        state.loading = true;

        httpLib
          .post("/api/account/join", state.account)
          .then(() => {
            state.loading = false;
            commLib.message.show("success", " . .");
            props.callback("close-modal");
          })
          .catch((err) => {
            state.loading = false;

            switch (err.response?.status) {
              case 403:
                commLib.message.show("warning", "    .");
                break;

              case 404:
                commLib.message.show("warning", "   .");
                break;

              case 409:
                commLib.message.show("warning", "   .");
                break;

              default:
                commLib.message.show("danger", " .");
                break;
            }
          });
      }
    };

    onMounted(() => {
      setTimeout(() => {
        emailRef.value.focus();
      }, 250);
    });

    return { state, emailRef, keyup, send, verify, create };
  },
};
</script>
<style lang="scss" scoped>
.modal-join {
  padding: 25px;

  h1 {
    font-weight: bold;
    font-size: 50px;
  }

  h3 {
    margin-top: 25px;
  }

  input {
    height: 50px;
    border-radius: 0;

    &:focus {
      outline: 0;
      box-shadow: none;
      border-color: #146ece;
    }
  }

  .actions {
    padding-top: 10px;

    button {
      font-size: 16px;
      height: 50px;
    }

    .prev {
      padding-top: 15px;
      text-align: center;

      > a {
        cursor: pointer;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}
</style>
