<template v-if="state.init">
  <div class="component-component-box clear" :class="{ activated: state.component }" :position="position">
    <div class="search">
      <i class="fa fa-filter" />
      <input type="text" class="form-control" ref="filterRef" spellcheck="false" placeholder="Filter the categories. / Press Alt + T to focus." v-model="state.filter" />
    </div>
    <div class="wrapper" v-if="categories.length">
      <div class="categories">
        <ul class="no-select">
          <li :class="{ active: state.category === c }" v-for="(c, idx) in categories" :key="idx" @click="setCategory(c)">{{ c }}</li>
        </ul>
      </div>
      <div class="list" ref="listRef">
        <ul>
          <li v-for="(c, idx) in state.components" :class="{ active: state.component === c }" :key="idx">
            <div class="preview" :style="[c.trim ? 'padding:0;' : '']">
              <img :src="c.imgUrl + '?thumb=true'" @click="setComponent(c)" />
            </div>
            <div class="bottom">
              <div class="title">
                <b class="name">{{ state.category + " " + c.num }}</b>
                <span class="tags">
                  <span class="badge" :class="['badge-' + badgets[t.charCodeAt(0) % badgets.length]]" v-for="(t, idx) in c.tags" :key="idx">{{ t }}</span>
                </span>
              </div>
            </div>
            <div class="icons">
              <i class="remove fa fa-trash" title="Remove" @click="remove(c.num)" v-if="custom"></i>
              <i class="upload fa fa-upload" :title="sector === 'template' ? 'Set template' : 'Append to root'" @click="runCommand(c.num)"></i>
              <i class="zoom fa fa-search" title="Zoom" @click="zoom(c.imgUrl, idx, c.num, c.trim)"></i>
              <i class="info fa fa-info" title="Show info" @click="read($event)"></i>
              <i class="unread" title="Hide info" @click="read($event)">&times;</i>
            </div>
            <div class="desc">
              <div class="wrapper">{{ custom ? `  ${sector === "template" ? "" : ""}.` : c.desc }}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="empty" v-else>
      <div class="inner">
        <span v-if="custom">  </span>
        <span>{{ sector === "template" ? "" : "" }} .</span>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, nextTick, reactive, ref } from "vue";
import { useStore } from "vuex";
import httpLib from "../libs/httpLib";
import commLib from "../libs/commonLib";
import global from "../global";
import $ from "jquery";

export default {
  props: {
    callback: Function,
    sector: String,
    position: String,
    custom: Boolean,
  },
  setup(props) {
    const store = useStore();
    const state = reactive({
      init: false,
      filter: "",
      category: "",
      categories: [],
      component: null,
      components: [],
      command: null,
    });

    const badgets = ["primary", "success", "danger", "warning", "secondary", "info", "navy", "ocean", "yellowgreen", "purple", "dark"];
    const filterRef = ref(null);
    const inputRef = ref(null);
    const listRef = ref(null);

    const categories = computed(() => {
      const result = state.categories.filter((x) => {
        return x.includes(state.filter);
      });

      return result;
    });

    const copy = () => {
      commLib.copy(inputRef.value.value);
      commLib.message.show("success", " .");
    };

    const focus = () => {
      nextTick(() => {
        filterRef.value.select();
      });
    };

    const read = (e) => {
      e.target.parentNode.parentNode.classList.toggle("read");
    };

    const runCommand = (num) => {
      const command = `@root ${props.sector === "template" ? "template" : "append"} ${state.category} ${num}`;
      store.state.refs.console.commandRef.runCommands(command);
    };

    const zoom = (imgUrl, idx, num, trim) => {
      props.callback(
        "open-modal",
        "viewer",
        "full",
        {
          idx: idx,
          list: state.components,
          category: state.category,
        },
        trim
      );
    };

    const closeComponent = () => {
      state.component = null;
      listRef.value.scrollTop = 0;
    };

    const remove = (num) => {
      if (confirm("  ?")) {
        httpLib
          .delete(`/api/components/${props.sector}/${state.category}/${num}`)
          .then(() => {
            commLib.message.show("success", ".");
            closeComponent();
            init(true);
          })
          .catch(() => {
            commLib.message.show("error", " .");
          });
      }
    };

    const setComponent = (c) => {
      state.component = c;

      nextTick(() => {
        const paddingTop = Number(
          window
            .getComputedStyle(listRef.value, null)
            .getPropertyValue("padding-top")
            .replace("px", "")
        );

        const scrollTop = listRef.value.scrollTop + listRef.value.querySelector("li.active").getBoundingClientRect().y - listRef.value.getBoundingClientRect().y - paddingTop;
        $(listRef.value)
          .stop()
          .animate({ scrollTop: scrollTop }, 250);
      });
    };

    const setCategory = (c) => {
      if (state.category === c) {
        return;
      }

      state.category = c;
      state.component = null;
      setComponents();
    };

    const setCategories = (callback) => {
      httpLib.get(`/api/components/${props.sector}`, { custom: props.custom }).then((res) => {
        state.categories = res.data;

        if (typeof callback === "function") {
          callback();
        }
      });
    };

    const setComponents = () => {
      state.components = [];

      httpLib.get(`/api/components/${props.sector}/${state.category}`, { custom: props.custom }).then((res) => {
        if (Array.isArray(res.data)) {
          const components = res.data;

          for (let c of components) {
            c.imgUrl = `${global.site.url}/components/preview/${props.sector}/${state.category}/${c.num}`;
          }

          state.components = components;
        }
      });
    };

    const init = (force) => {
      if (state.init && !force) {
        return;
      }

      setCategories(() => {
        if (!state.category || !state.categories.includes(state.category)) {
          state.category = state.categories[0];
        }

        if (state.category) {
          setComponents();
        }
      });

      state.init = true;
    };

    return {
      store,
      state,
      init,
      categories,
      badgets,
      filterRef,
      inputRef,
      listRef,
      copy,
      read,
      zoom,
      runCommand,
      focus,
      setCategory,
      setCategories,
      setComponent,
      closeComponent,
      remove,
    };
  },
  watch: {
    categories: {
      handler() {
        if (this.state.filter && this.categories.length) {
          this.setCategory(this.categories[0]);
        }
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.component-component-box {
  height: 100%;
  color: #fff;
  font-size: 14px;
  position: relative;
  padding-top: 40px;

  > .search {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 40px;
    background: #262626;
    padding-left: 25px;
  }

  > .search i {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 25px;
    color: #545454;
    text-align: right;
    line-height: 40px;
  }

  > .search input {
    height: 100%;
    font-size: 13px;
    border-radius: 0;
    background: none;
    border: 0;
    box-shadow: none;
    padding-left: 10px;
    outline: 0;
    color: #fff;
  }

  > .wrapper {
    height: 100%;
    position: relative;

    > div {
      float: left;
      height: 100%;
      overflow: auto;
      -ms-overflow-style: none; /* IE and Edge */
      scrollbar-width: none; /* Firefox */

      &.categories {
        position: relative;
        overflow: auto;
        min-width: 100px;
        border-right: 1px solid #3c3c3c;
        z-index: 2;

        ul {
          list-style: none;
          margin: 0;
          padding: 0;

          li {
            cursor: pointer;
            padding: 10px 14px;
            border-bottom: 1px solid #3c3c3c;
            font-size: 13px;

            &:last-child {
              border-bottom: 0;
            }

            &.active {
              color: #0095ff;
            }
          }
        }
      }

      &.list {
        position: absolute;
        padding: 10px 15px 15px 110px;
        width: 100%;
        left: 0;
        top: 0;

        > ul {
          list-style: none;
          margin: 0;
          padding: 0;

          > li {
            display: inline-block;
            width: 345px;
            max-width: 100%;
            margin: 5px;
            background: #fff;
            border: 1px solid #3c3c3c;
            color: #333;
            vertical-align: top;
            position: relative;

            > .preview {
              padding: 10px;
              position: relative;
              font-size: 0;
              min-height: 34px;
              text-align: center;
            }

            > .bottom {
              padding: 10px 12.5px;
              background: #f7f7f7;
              border-top: 1px solid #ced4da;

              > .title {
                padding: 2.5px 0;

                > .tags {
                  font-family: initial;
                  float: right;
                  padding: 2.5px 0;
                  text-align: right;
                  line-height: 15px;

                  > .badge {
                    border-radius: 0;
                    font-size: 10px;
                    margin-right: 1px;

                    &:last-child {
                      margin-right: 0;
                    }
                  }
                }

                &:after {
                  content: " ";
                  display: table;
                  clear: right;
                }
              }
            }

            > .icons {
              display: none;
              position: absolute;
              top: 0;
              right: 0;
              text-align: right;
              width: 100%;
              z-index: 1;

              > i {
                background: rgba(0, 0, 0, 0.5);
                display: inline-block;
                color: #fff;
                cursor: pointer;
                vertical-align: middle;
                text-align: center;
                width: 34px;
                height: 34px;
                line-height: 34px;

                &.unread {
                  font-size: 25px;
                  font-style: normal;
                  line-height: 1.4;
                }

                &:hover {
                  background: rgba(0, 0, 0, 0.75);
                }
              }
            }

            > .desc {
              display: none;
              position: absolute;
              left: 0;
              top: 0;
              width: 100%;
              height: 100%;
              background: rgba(0, 0, 0, 0.85);
              color: #fff;

              > .wrapper {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                padding: 15px;
              }
            }

            &:hover {
              > .icons {
                display: inline-block;
              }
            }

            &:not(.read) {
              > .icons > i.unread {
                display: none;
              }
            }

            &.read {
              > .icons {
                > i {
                  &.info {
                    display: none;
                  }

                  &.unread {
                    display: inline-block;
                  }

                  &:hover {
                    background: rgba(58, 58, 58, 0.75);
                  }
                }
              }

              > .desc {
                display: block;
              }
            }
          }
        }
      }

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }

  > .empty {
    display: table;
    height: 100%;
    width: 100%;

    > .inner {
      display: table-cell;
      text-align: center;
      vertical-align: middle;
    }
  }

  &.activated {
    > .wrapper {
      > div {
        &.center {
          width: 475px;
          border-right: 1px solid #3c3c3c;

          ul {
            li {
              opacity: 0.25;

              &.active {
                opacity: 1;
              }
            }
          }
        }

        &.right {
          padding-left: 475px;
          left: 0;
        }
      }
    }
  }

  &[position="side"] {
    > .wrapper {
      > div {
        &.center {
          > ul > li {
            width: 400px;
          }
        }
      }
    }

    &.activated {
      > .wrapper {
        > div {
          &.center {
            display: none;
          }

          &.right {
            padding-left: 100px;

            > .wrapper {
              > div {
                width: 100%;
                height: auto;
                float: none;

                &.side {
                  padding-top: 0;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
