<template>
  <div class="modal-comment">
    <div class="header">
      <div class="inner">
        <div class="title">
          <i class="fa fa-commenting-o"></i>
          <span>Leave a comment.</span>
        </div>
        <button class="submit btn btn-point" tabindex="20" @click="submit()">Submit</button>
        <button class="refresh btn" @click="set(true)">
          <i class="fa fa-refresh"></i>
        </button>
      </div>
      <textarea class="form-control" placeholder="Type your comment here." v-model="state.message" ref="textRef" tabindex="10"></textarea>
    </div>
    <div class="comments" v-if="state.comment.count">
      <ul>
        <li v-for="(c, idx) in state.comment.list" :key="idx">
          <div class="symbol font-parisienne">{{ c.writer === "unknown" ? "?" : c.writer[0] }}</div>
          <div class="header">
            <span class="writer">{{ c.writer }}</span>
            <div class="actions">
              <button class="remove" @click="remove(c.id)">
                <i class="fa fa-trash" v-if="c.mid === store.state.account.mid"></i>
              </button>
            </div>
            <span class="date" :title="c.insDate">{{ c.ago }}</span>
          </div>
          <div class="message">{{ c.message }}</div>
        </li>
      </ul>
      <div class="more" v-if="state.comment.count && state.comment.list.length < state.comment.count">
        <button @click="expand()">Load more</button>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, ref } from "@vue/reactivity";
import { onMounted } from "@vue/runtime-core";
import commLib from "../libs/commonLib";
import httpLib from "../libs/httpLib";
import { useStore } from "vuex";

export default {
  props: {},
  setup() {
    const store = useStore();
    const state = reactive({
      page: 1,
      message: "",
      comment: {
        count: 0,
        list: [],
      },
    });

    const textRef = ref();

    const expand = () => {
      state.page += 1;
      set();
    };

    const submit = () => {
      if (!state.message) {
        commLib.message.show("warning", " .");
        textRef.value.focus();
        return;
      }

      httpLib
        .post("/api/comments/submit", { message: state.message })
        .then(() => {
          state.message = "";
          commLib.message.show("success", " ");
          set(true);
        })
        .catch(() => {
          commLib.message.show("error", " .");
        });
    };

    const remove = (id) => {
      if (confirm("  ?")) {
        httpLib
          .delete("/api/comments/" + id)
          .then(() => {
            commLib.message.show("success", ".");
            set(true);
          })
          .catch(() => {
            commLib.message.show("error", " .");
          });
      }
    };

    const set = (init) => {
      if (init) {
        state.page = 1;
        state.comment.list = [];
      }

      const args = {
        message: state.message,
        page: state.page,
      };

      httpLib.get("/api/comments", args).then((res) => {
        if (res.data && Array.isArray(res.data.list)) {
          state.comment.count = res.data.count;
          state.comment.list.push(...res.data.list);
        }
      });
    };

    onMounted(() => {
      set(true);
      setTimeout(() => {
        textRef.value.focus();
      }, 250);
    });

    return { store, state, textRef, set, remove, expand, submit };
  },
};
</script>
<style lang="scss" scoped>
$backgroundColor: #f7f7f7;

.modal-comment {
  background: $backgroundColor;

  > .header {
    position: sticky;
    background: $backgroundColor;
    top: 0;
    left: 0;
    width: 100%;
    font-size: 20px;
    padding: 25px;
    z-index: 1;

    > .inner {
      margin-bottom: 15px;

      .title {
        float: left;

        > i {
          margin-right: 10px;
        }
      }

      &:after {
        content: "";
        display: table;
        clear: both;
      }

      > button {
        float: right;
        border-radius: 0;
        padding-top: 0;
        padding-bottom: 0;
        height: 38px;
        line-height: 38px;

        &.refresh {
          background: #fff;
          border: 1px solid #ddd;
          width: 38px;
          text-align: center;
          font-size: 13px;
          margin-right: 10px;
        }

        &.submit {
          width: 100px;
        }
      }
    }

    textarea {
      border-radius: 0;
      height: 100px !important;
      height: 100px;
      font-size: 14px;
      padding: 12.5px;
      resize: none;

      &:focus {
        outline: 0;
        box-shadow: none;
        border-color: #146ece;
      }
    }
  }

  .comments {
    padding: 10px 25px 25px 25px;

    ul {
      list-style: none;
      padding: 0;
      margin: 0;

      > li {
        background: #fff;
        position: relative;
        margin-bottom: 25px;
        border: 1px solid #ddd;
        padding: 30px 25px 25px 92.5px;

        .symbol {
          text-transform: uppercase;
          width: 60px;
          height: 60px;
          line-height: 60px;
          font-size: 21px;
          font-weight: bold;
          color: #1259a7;
          text-align: center;
          position: absolute;
          left: 15px;
          top: 25px;
          border: 1px solid #dedede;
        }

        > .header {
          margin-bottom: 7.5px;
          font-size: 14px;
          position: relative;

          .writer {
            font-weight: bold;
          }

          .actions {
            position: absolute;
            right: 0;
            top: 30px;

            .remove {
              cursor: pointer;
              padding: 0;
              border: 0;
              color: #666;
            }
          }

          .date {
            float: right;
            margin-left: 10px;
            text-align: right;
          }
        }

        .message {
          font-size: 14px;
          color: #262626;
          min-height: 21px;
          line-break: anywhere;
        }

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    > .more {
      text-align: center;
      border-top: 1px solid #ddd;
      padding-top: 10px;
      margin-top: 35px;

      button {
        cursor: pointer;
        border: none;
        background: none;
        padding: 15px;
      }
    }
  }
}
</style>
