<template>
  <div class="modal-viewer" :style="[state.slide.trim ? 'padding:0' : '']">
    <img :src="state.slide.url" @load="setStyle($event)" />
    <div class="angle left" :style="state.angleStyle" @click="move('prev')">
      <img src="/img/ico.angle.left.white.png" />
    </div>
    <div class="angle right" :style="state.angleStyle">
      <img src="/img/ico.angle.right.white.png" @click="move('next')" />
    </div>
    <div class="name">{{ state.slide.category }} {{ state.slide.num }}</div>
    <div class="run-btn" title="Append to root" @click="run()">
      <i class="fa fa-upload"></i>
    </div>
  </div>
</template>
<script>
import { reactive } from "@vue/reactivity";
import { onMounted } from "@vue/runtime-core";
import { useStore } from "vuex";
export default {
  props: {
    data: Object,
  },
  setup(props) {
    const store = useStore();
    const state = reactive({
      slide: {
        idx: 0,
        url: "",
        category: "",
        num: 0,
        list: [],
        trim: false,
      },
      angleStyle: {
        display: "none",
        top: 0,
      },
    });

    const setStyle = (e) => {
      state.angleStyle.display = "block";
      state.angleStyle.top = e.target.parentNode.offsetHeight / 2 + "px";
    };

    const run = () => {
      const command = `@root append ${state.slide.category} ${state.slide.num}`;
      store.state.refs.console.commandRef.runCommands(command);
    };

    const move = (direction) => {
      if (direction === "next" && state.slide.idx < state.slide.list.length - 1) {
        state.slide.idx += 1;
      } else if (direction === "prev" && state.slide.idx > 0) {
        state.slide.idx -= 1;
      } else {
        return;
      }

      state.slide.url = state.slide.list[state.slide.idx].imgUrl;
      state.slide.num = state.slide.list[state.slide.idx].num;
      state.slide.trim = state.slide.list[state.slide.idx].trim;
    };

    onMounted(() => {
      const idx = props.data.idx;
      const list = props.data.list;
      state.slide.idx = idx;
      state.slide.url = list[idx].imgUrl;
      state.slide.num = list[idx].num;
      state.slide.trim = list[idx].trim;
      state.slide.list = list;
      state.slide.category = props.data.category;
    });

    return { state, run, move, setStyle };
  },
};
</script>
<style lang="scss" scoped>
.modal-viewer {
  text-align: center;
  padding: 25px;

  img {
    max-width: 100%;
  }

  .angle {
    position: absolute;
    top: 50%;
    cursor: pointer;
    margin-top: -33.5px;
    display: none;

    &.left {
      left: -75px;
    }

    &.right {
      right: -75px;
    }
  }

  .name {
    position: absolute;
    top: -60px;
    color: #fff;
    text-align: center;
    width: 100%;
    left: 0;
    font-size: 20px;
    padding: 15px 0;
  }

  .run-btn {
    position: absolute;
    cursor: pointer;
    top: -50px;
    right: 50px;
    margin-right: 1px;
    font-size: 21px;
    line-height: 50px;
    width: 50px;
    color: #fff;
    text-align: center;
    background: rgba(0, 0, 0, 0.21);
    transition: background 0.25s;
  }
}
</style>
