import $ from "jquery";

const lib = {
    isNumeric: (str) => {
        if (typeof str === "number") {
            str = str.toString();
        }
        else if (typeof str != "string") {
            return false;
        }

        return !isNaN(str) && !isNaN(parseFloat(str));
    },

    isOnlyAlphabetString: (str) => {
        return /^[a-zA-Z]+$/.test(str);
    },

    isValidEmail: (email) => {
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    },

    getStrToArray: (str, separator1, separator2) => {
        const result = [];
        const arr1 = str.split(separator1);

        for (let x of arr1) {
            const arr2 = x.split(separator2);

            for (let y of arr2) {
                if (y) {
                    result.push(y);
                }
            }
        }

        return result;
    },

    getRenewed: (value) => {
        if (!value) {
            return value;
        }

        return JSON.parse(JSON.stringify(value));
    },

    getTextReplacedDoubleSpacesToSingle: (text) => {
        if (text.includes("  ")) {
            text = text.replaceAll("  ", " ");
            return lib.getTextReplacedDoubleSpacesToSingle(text);
        }

        return text;
    },

    timer: {},

    message: {
        show: (head, txt) => {
            let $message = $('#commonMessage');
            const width = 420;
            lib.message.hide();

            if ($message.length) {
                $message.show();
            }
            else {
                const html = `<div id="commonMessage" style="border:1px solid #292929;position:fixed;bottom:25px;width:${width}px;background:rgba(0,0,0,0.85);padding:15px 20px 30px 20px;color:#fff;z-index:1001">
                                  <div class="header" style="font-weight:bold;font-size:14px;margin-left:-5px;">
                                      <i style="font-size:11px;margin-right:3px;position:relative;"></i>
                                      <span></span>
                                  </div>
                                  <div class="text" style="font-size:15px;margin-top:5px;padding-left:12px;"></div>
                                  <span class="close-btn" style="position:absolute;right:8px;top:3px;font-size:27px;cursor:pointer;line-height:1;">&times;</span>
                              </div>`;

                document.body.insertAdjacentHTML('beforeend', html);
                $message = $('#commonMessage');

                $message.find('.close-btn').on('click', () => {
                    lib.message.hide();
                });
            }

            $message.css('right', -(width));
            $message.find(".header > i").removeAttr("class").addClass(`fa ${head === 'success' ? 'fa-check' : 'fa-exclamation-triangle'}`)
            $message.find(".header > span").text(head[0].toUpperCase() + head.substring(1));
            $message.find(".text").text(txt);

            $message.stop().animate({ right: 0 }, () => {
                clearInterval(lib.timer.message);

                lib.timer.message = setTimeout(() => {
                    $message.stop().animate({ right: width * -1 }, () => {
                        lib.message.hide();
                    });
                }, 5000)
            });
        },
        hide: () => {
            clearInterval(lib.timer.message);
            $('#commonMessage').hide();
        }
    },

    isTextSelected: (elem) => {
        if (typeof elem.selectionStart == "number") {
            return elem.selectionStart == 0 && elem.selectionEnd == elem.value.length;
        } else if (typeof document.selection != "undefined") {
            return document.selection.createRange().text == elem.value;
        }
    },

    copy: (text) => {
        const $copyText = document.createElement("textarea");
        $copyText.setAttribute("id", "copyText");
        $copyText.value = text;
        document.body.appendChild($copyText);
        $copyText.select();
        document.execCommand("copy");
        $copyText.remove();
        lib.message.show("success", " .");
    },

    postForm: (url, args) => {
        const $form = document.createElement("form");
        $form.setAttribute("action", url);
        $form.setAttribute("method", "post");
        $form.setAttribute("target", "_blank");

        if (args && typeof args === 'object' && Object.keys(args).length) {
            for (let i in args) {
                const $input = document.createElement("input");
                $input.setAttribute("type", "hidden");
                $input.setAttribute("name", i);
                $input.setAttribute("value", args[i]);
                $form.appendChild($input);
            }
        }

        document.body.appendChild($form);
        $form.submit();
        $form.remove();
    },

    cookie: {
        get: (name) => {
            if (document.cookie.length > 0) {
                let start = document.cookie.indexOf(name + "=");
                let end;

                if (start != -1) {
                    start = start + name.length + 1;
                    end = document.cookie.indexOf(";", start);

                    if (end == -1) {
                        end = document.cookie.length;
                    }

                    return unescape(document.cookie.substring(start, end));
                }
            }

            return "";
        },
        set: (name, value, days) => {
            var expires;

            if (days) {
                var date = new Date();
                date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
                expires = "; expires=" + date.toGMTString();
            }
            else {
                expires = "";
            }

            document.cookie = name + "=" + value + expires + "; path=/;";
        },
        remove: (name) => {
            lib.cookie.set(name, "", -1);
        }
    }
}

export default lib;