<template>
  <div class="component-console" :class="{ 'no-select': state.dragging, closed: state.folded }" ref="consoleRef" :style="state.style" :position="state.position">
    <div class="header clear">
      <div class="addons">
        <Toggle text="tag" :active="store.state.toggle.tag" :toggle="toggleTag" title="Tag (Alt + V)" :disabled="!store.state.page.name" ref="toggleTagRef" />
        <Toggle text="line" :active="store.state.toggle.outline" :toggle="toggleOutline" title="Line (Alt + V)" :disabled="!store.state.page.name" ref="toggleOutlineRef" />
        <span class="fa fa-repeat" title="Refresh (Alt + R)" aria-hidden="true" :class="{ disabled: !store.state.page.name }" @click="callback('refresh')"></span>
        <span class="fa fa-backward" title="Undo (Alt + Z)" aria-hidden="true" :class="{ disabled: !store.state.page.name }" @click="callback('undo')"></span>
        <span class="fa fa-forward" title="Redo (Alt + Shift + Z)" aria-hidden="true" :class="{ disabled: !store.state.page.name }" @click="callback('redo')"></span>
        <span class="fa fa-comment-o" title="Comments" aria-hidden="true" :class="{ disabled: !store.state.account.login }" @click="store.state.account.login && callback('open-modal', 'comment', 'large')"></span>
        <span class="position" title="Change Position (Alt + A)" @click="changePosition()">
          <img src="/img/ico.console.side.png" v-if="state.position !== 'side'" />
          <img src="/img/ico.console.bottom.png" v-else-if="state.position !== 'bottom'" />
        </span>
        <span class="full" title="Full Screen (Alt + F)" @click="setFullScreen()">
          <img src="/img/ico.full.png" />
        </span>
        <span class="fold" :title="(!state.folded ? 'Fold' : 'Unfold') + ' (Alt + D)'" @click="fold()">
          <i class="fa fa-angle-right right" aria-hidden="true"></i>
          <i class="fa fa-angle-left left" aria-hidden="true"></i>
          <i class="fa fa-angle-down down" aria-hidden="true"></i>
          <i class="fa fa-angle-up up" aria-hidden="true"></i>
        </span>
      </div>
      <div class="tabs">
        <ul class="no-select">
          <li v-for="(t, idx) in tabs" :key="idx" :class="{ active: t === state.activeTab, disabled: !store.state.page.name }" @click="selectTab(t)" :title="t[0].toUpperCase() + t.substring(1) + ' (Alt + ' + (idx + 1) + ')'">
            <div>{{ t }}</div>
          </li>
        </ul>
      </div>
    </div>
    <div class="core" :style="{ paddingBottom: state.activeTab === 'command' ? 0 : '41px' }">
      <Command ref="commandRef" :page="store.state.page.name" :login="store.state.account.login" :position="state.position" :folded="state.folded" :callback="callback" :active="!state.folded && state.activeTab === 'command'" />
      <Editor
        :page="store.state.page.name"
        :position="state.position"
        v-model:selectionTagName="state.editor.selectionTagName"
        v-model:selectionTagIdx="state.editor.selectionTagIdx"
        :callback="callback"
        v-show="state.activeTab === 'code'"
        ref="editorRef"
        :key="state.key"
      />
      <File ref="fileRef" :page="store.state.page.name" :position="state.position" v-show="state.activeTab === 'files'" :callback="callback" :key="state.key" />
      <Box ref="componentBoxRef" sector="component" :custom="false" :position="state.position" v-show="state.activeTab === 'components'" :callback="callback" :key="state.key" />
      <Box ref="templateBoxRef" sector="template" :custom="false" :position="state.position" v-show="state.activeTab === 'templates'" :callback="callback" :key="state.key" />
      <Box ref="myComponentBoxRef" sector="component" :custom="true" :position="state.position" v-show="state.activeTab === 'my-components'" :callback="callback" :key="state.key" />
      <Box ref="myTemplateBoxRef" sector="template" :custom="true" :position="state.position" v-show="state.activeTab === 'my-templates'" :callback="callback" :key="state.key" />
    </div>
    <div class="grab" ref="grabRef" v-show="!state.folded && !state.fullScreen"></div>
  </div>
</template>

<script>
import { nextTick, onMounted, reactive, ref } from "vue";
import { useStore } from "vuex";
import Toggle from "./Toggle";
import Editor from "./Editor";
import File from "./File";
import Command from "./Command";
import Box from "./Box";
import commLib from "../libs/commonLib";

export default {
  components: { Toggle, Command, Editor, File, Box },
  props: {
    callback: Function,
  },
  setup(props) {
    const store = useStore();
    const state = reactive({
      key: 1,
      activeTab: null,
      folded: false,
      fullScreen: false,
      dragging: false,
      position: null,
      style: {
        width: null,
        height: null,
      },
      editor: {
        selectionTagName: null,
        selectionTagIdx: null,
      },
    });

    const tabs = ["command", "code", "files", "components", "templates", "my-components", "my-templates"];

    const sideSize = 500;
    const sideMinSize = 420;
    const sideMaxSize = window.innerWidth - 300;

    const bottomSize = 370;
    const bottomMinSize = 260;
    const bottomMaxSize = window.innerHeight - 300;

    const defaultOpened = false;
    const defaultPosition = "side";
    const cookieOpened = commLib.cookie.get("cookieOpened");
    const cookiePosition = commLib.cookie.get("cookiePosition");

    // refs
    const grabRef = ref(null);
    const consoleRef = ref(null);
    const toggleTagRef = ref(null);
    const toggleOutlineRef = ref(null);
    const textRef = ref(null);
    const commandRef = ref(null);
    const editorRef = ref(null);
    const fileRef = ref(null);
    const componentBoxRef = ref(null);
    const templateBoxRef = ref(null);
    const myComponentBoxRef = ref(null);
    const myTemplateBoxRef = ref(null);

    // methods
    const selectTab = (tab, prevent) => {
      if (!store.state.page.name) {
        return;
      }

      if (state.folded) {
        fold();
      }

      if (state.activeTab === tab) {
        return;
      }

      state.activeTab = tab;

      switch (state.activeTab) {
        case "command":
          nextTick(() => {
            commandRef.value.adjust();
          });
          break;

        case "code":
          editorRef.value.init();
          break;

        case "files":
          fileRef.value.init();
          break;

        case "components":
          componentBoxRef.value.init();

          if (!prevent) {
            componentBoxRef.value.focus();
          }
          break;

        case "templates":
          templateBoxRef.value.init();

          if (!prevent) {
            templateBoxRef.value.focus();
          }
          break;

        case "my-components":
          myComponentBoxRef.value.init();

          if (!prevent) {
            myComponentBoxRef.value.focus();
          }
          break;

        case "my-templates":
          myTemplateBoxRef.value.init();

          if (!prevent) {
            myTemplateBoxRef.value.focus();
          }
          break;

        default:
          break;
      }
    };

    const toggleTag = (active) => {
      store.commit("setToggle", {
        tag: active,
      });

      if (active) {
        commLib.cookie.remove("webviewTag");
        store.state.refs.webview.postMessage("set-tags");
        store.state.refs.webview.postMessage("remove-class", "no-tag");
      } else {
        commLib.cookie.set("webviewTag", "N");
        store.state.refs.webview.postMessage("add-class", "no-tag");
      }
    };

    const toggleOutline = (active) => {
      store.commit("setToggle", {
        outline: active,
      });

      if (!active) {
        commLib.cookie.set("webviewOutline", "N");
        store.state.refs.webview.postMessage("add-class", "no-outline");
      } else {
        commLib.cookie.remove("webviewOutline");
        store.state.refs.webview.postMessage("remove-class", "no-outline");
      }
    };

    const fold = (fold) => {
      state.folded = fold === undefined ? !state.folded : fold;

      if (state.folded) {
        state.fullScreen = false;
      }

      if (state.position === "side") {
        let value = 0;

        if (!state.folded) {
          const sessionWidth = sessionStorage.getItem("consoleWidth");
          value = sessionWidth ? sessionWidth : `${sideSize}px`;
        } else {
          value = "60px";
        }

        state.style.height = "100%";
        state.style.width = value;
        props.callback("set-style", { paddingLeft: value, paddingBottom: 0 });
      } else {
        let value = 0;

        if (!state.folded) {
          const sessionHeight = sessionStorage.getItem("consoleHeight");
          value = sessionHeight ? sessionHeight : `${bottomSize}px`;
        } else {
          value = "88px";
        }

        state.style.height = value;
        state.style.width = "100%";
        props.callback("set-style", { paddingLeft: 0, paddingBottom: value });
      }

      if (state.activeTab === "command") {
        nextTick(() => {
          commandRef.value?.resize();
          commandRef.value?.textareaRef.focus();
        });
      }

      if (!state.folded === defaultOpened) {
        commLib.cookie.remove("cookieOpened");
      } else {
        commLib.cookie.set("cookieOpened", !state.folded ? "Y" : "N", 7);
      }
    };

    const setFullScreen = () => {
      state.fullScreen = !state.fullScreen;

      if (state.fullScreen) {
        state.folded = false;

        if (state.position === "side") {
          props.callback("set-style", { paddingLeft: 0 });
          state.style.width = "100%";
        } else {
          props.callback("set-style", { paddingBottom: 0 });
          state.style.height = "100%";
        }
      } else {
        if (state.position === "side") {
          const sessionWidth = sessionStorage.getItem("consoleWidth");
          const value = sessionWidth ? sessionWidth : `${sideSize}px`;
          props.callback("set-style", { paddingLeft: value });
          state.style.width = value;
        } else {
          const sessionHeight = sessionStorage.getItem("consoleHeight");
          const value = sessionHeight ? sessionHeight : `${bottomSize}px`;
          props.callback("set-style", { paddingBottom: value });
          state.style.height = value;
        }
      }

      if (state.activeTab === "command") {
        nextTick(() => {
          commandRef.value.resize();
        });
      }
    };

    const changePosition = (position) => {
      state.position = position === undefined ? (state.position === "side" ? "bottom" : "side") : position;
      fold(state.folded);

      if (state.activeTab === "command") {
        nextTick(() => {
          commandRef.value?.resize();
        });
      }

      if (state.position !== defaultPosition) {
        commLib.cookie.set("cookiePosition", state.position);
      } else {
        commLib.cookie.remove("cookiePosition");
      }
    };

    const reloadComponents = () => {
      state.key += 1;

      nextTick(() => {
        componentBoxRef.value.setCategories();
        templateBoxRef.value.setCategories();
        myComponentBoxRef.value.setCategories();
        myTemplateBoxRef.value.setCategories();
      });
    };

    // created
    state.activeTab = tabs[0];
    state.position = defaultPosition;

    if (cookiePosition) {
      changePosition(cookiePosition);
    } else {
      changePosition(defaultPosition);
    }

    if (cookieOpened !== defaultOpened) {
      fold(cookieOpened !== "Y");
    }

    // mounted
    onMounted(() => {
      let startX, startWidth, startY, startHeight;

      const initDrag = (e) => {
        if (state.position === "bottom") {
          startY = e.clientY;
          startHeight = parseInt(document.defaultView.getComputedStyle(consoleRef.value).height, 10);
        } else {
          startX = e.clientX;
          startWidth = parseInt(document.defaultView.getComputedStyle(consoleRef.value).width, 10);
        }

        if (store.state.page.name) {
          store.state.refs.webview.state.dragging = true;
        }

        state.dragging = true;

        document.documentElement.addEventListener("mousemove", doDrag, false);
        document.documentElement.addEventListener("mouseup", stopDrag, false);
      };

      const doDrag = (e) => {
        if (state.position === "side") {
          let value = startWidth + e.clientX - startX;

          if (value < sideMinSize) value = sideMinSize;
          if (value > sideMaxSize) value = sideMaxSize;

          value += "px";
          props.callback("set-style", { paddingLeft: value });
          state.style.width = value;

          sessionStorage.setItem("consoleWidth", value);
        } else {
          let value = startHeight - e.clientY + startY;

          if (value < bottomMinSize) value = bottomMinSize;
          if (value > bottomMaxSize) value = bottomMaxSize;

          value += "px";
          props.callback("set-style", { paddingBottom: value });
          state.style.height = value;

          sessionStorage.setItem("consoleHeight", value);
        }
      };

      const stopDrag = () => {
        state.dragging = false;

        document.documentElement.removeEventListener("mousemove", doDrag, false);
        document.documentElement.removeEventListener("mouseup", stopDrag, false);

        if (state.activeTab === "command") {
          commandRef.value.resize();
        }

        if (store.state.page.name) {
          store.state.refs.webview.state.dragging = false;
        }
      };

      grabRef.value.addEventListener("mousedown", initDrag, false);
    });

    return {
      store,
      state,
      tabs,
      consoleRef,
      commandRef,
      editorRef,
      fileRef,
      componentBoxRef,
      templateBoxRef,
      myComponentBoxRef,
      myTemplateBoxRef,
      grabRef,
      textRef,
      toggleTagRef,
      toggleOutlineRef,
      toggleTag,
      toggleOutline,
      fold,
      setFullScreen,
      changePosition,
      selectTab,
      reloadComponents,
    };
  },
};
</script>
<style lang="scss" scoped>
.component-console {
  max-height: 100%;
  max-width: 100%;
  overflow: hidden;
  position: absolute;
  left: 0;
  width: 100%;
  bottom: 0;

  > .header {
    position: absolute;
    background: #000;
    color: #fff;
    left: 0;
    top: 0;
    font-size: 14px;
    width: 100%;

    > .tabs {
      float: left;

      ul {
        list-style: none;
        padding: 0;
        margin: 0;
        white-space: nowrap;
        overflow-y: hidden;
        overflow-x: auto;
        -ms-overflow-style: none; /* IE and Edge */
        scrollbar-width: none; /* Firefox */

        > li {
          color: #aaa;
          cursor: pointer;
          display: inline-block;
          padding: 14px;

          &:not(.disabled):hover {
            color: #fff;
          }

          &.active {
            color: #fff;
            opacity: 1;

            > div {
              display: inline-block;
              position: relative;

              &:after {
                content: " ";
                border-bottom: 1px solid #fff;
                position: absolute;
                left: 0;
                width: 100%;
                bottom: 0;
              }
            }
          }
        }

        &::-webkit-scrollbar {
          display: none;
        }
      }
    }

    > .addons {
      float: right;
      padding: 5px 4px 5px 14px;
      position: relative;

      > div {
        margin: 8px 15px 5px 0;
      }

      > span {
        vertical-align: top;
        color: #fff;
        padding: 10px;
        cursor: pointer;
        line-height: 1;
        font-size: 17px;
        display: inline-block;
        text-align: center;

        &.fold {
          font-size: 25px;
          margin-top: -4px;

          > i {
            display: none;
          }
        }
      }
    }
  }

  > .grab {
    cursor: n-resize;
    height: 10px;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    z-index: 5;
  }

  > .core {
    height: 100%;
    position: relative;
    background: #1e1e1e;

    > * {
      background: inherit;
    }
  }

  &[position="side"] {
    height: 100% !important;
    padding-top: 100px;

    > .header {
      height: 100px;
      padding: 0;

      > .addons {
        float: none;
        border-bottom: 1px solid #3c3c3c;
        height: 50%;
        text-align: right;

        > div {
          float: left;
        }

        > span {
          &.fold i {
            &.right {
              display: none;
            }

            &.left {
              display: inline-block;
            }
          }
        }
      }

      > .tabs {
        height: 50%;
        width: 100%;
        overflow: hidden;
      }
    }

    > .grab {
      cursor: e-resize;
      height: 100%;
      position: absolute;
      width: 10px;
    }

    &.closed {
      > .header {
        height: 100%;

        > .addons {
          height: 100%;
          padding-left: 5px;
          padding-right: 5px;
          text-align: center;

          > div {
            width: 100%;
            margin-right: 0;
            float: none;

            &:nth-child(2) {
              margin-bottom: 12.5px;
            }
          }

          > span {
            display: block;

            &.fold i {
              &.right {
                display: inline-block;
              }

              &.left {
                display: none;
              }
            }
          }
        }
      }

      > .core {
        display: none;
      }
    }
  }

  &[position="bottom"] {
    width: 100% !important;
    padding-top: 50px;

    > .header {
      height: 50px;

      > .addons {
        > span {
          &.fold i {
            &.up {
              display: none;
            }

            &.down {
              display: inline-block;
            }
          }
        }
      }
    }

    &.closed {
      > .header {
        > .addons {
          > span {
            &.fold i {
              &.up {
                display: inline-block;
              }

              &.down {
                display: none;
              }
            }
          }
        }
      }
    }
  }
}
</style>
