<template v-if="state.init">
  <div class="component-editor" :position="position">
    <div ref="wrapperRef" class="wrapper"></div>
    <div class="addon">
      <ul>
        <li :class="{ active: state.lang === l }" :key="idx" v-for="(l, idx) in editorLangs" @click="selectLang(l)" :title="l + ' (Alt + Shift + ' + (idx + 1) + ')'">{{ l === "javascript" ? "js" : l }}</li>
      </ul>
      <button class="btn btn-primary" title="Submit (Ctrl + S)" @click="pushCodes">submit</button>
    </div>
  </div>
</template>
<script>
import loader from "@monaco-editor/loader";
import { onUnmounted, reactive, ref } from "vue";
import { useStore } from "vuex";
import httpLib from "../libs/httpLib";
import commLib from "../libs/commonLib";
import $ from "jquery";

const monaco = {
  editor: null,
  codeEditor: null,
};

export default {
  props: {
    page: String,
    callback: Function,
    selectionTagName: String,
    selectionTagIdx: Number,
    position: String,
  },
  emits: ["update:selectionTagName", "update:selectionTagIdx"],
  setup(props, context) {
    const store = useStore();
    const state = reactive({
      init: false,
      key: 1,
      codes: {
        html: "",
        css: "",
        javascript: "",
      },
      lang: "",
    });

    const editorLangs = ["html", "css", "javascript"];
    const wrapperRef = ref(null);

    const setCodes = () => {
      let args = {
        page: props.page,
        seq: store.state.page.seq,
        nid: store.state.page.nid,
      };

      httpLib.get("/api/pages/node", args).then((res) => {
        if (typeof res.data === "object" && Object.keys(res.data).length) {
          state.codes = res.data;
          setMonacoValue();
        } else if (res.data === "none") {
          commLib.message.show("warning", "   .");
        }
      });
    };

    const setMonacoValue = () => {
      if (monaco.editor) {
        const model = monaco.editor.editor.getModels()[0];

        if (!model) {
          return;
        }

        model.setValue(state.codes[state.lang]);
        monaco.editor.editor.setModelLanguage(model, state.lang);

        setTimeout(() => {
          monaco.codeEditor.setScrollPosition({ scrollTop: 0 });
          monaco.codeEditor.getAction("editor.action.formatDocument").run();

          if (props.selectionTagName) {
            setTimeout(() => {
              setSelection(props.selectionTagName, props.selectionTagIdx);
              context.emit("update:selectionTagName", "");
              context.emit("update:selectionTagIdx", 0);
            }, 250);
          }
        }, 250);
      }
    };

    const selectLang = (lang) => {
      state.lang = lang;
      setMonacoValue();
    };

    const pushCodes = () => {
      const args = {
        page: store.state.page.name,
        seq: store.state.page.seq,
        codes: JSON.stringify(state.codes),
      };

      httpLib
        .post("/api/pages/codes", args)
        .then(() => {
          commLib.message.show("success", "  .");
          store.state.refs.webview.load(store.state.page.seq + 1, true);
        })
        .catch((err) => {
          switch (err.response?.status) {
            case 400:
              commLib.message.show("error", "    .");
              break;

            default:
              commLib.message.show("error", " .");
              break;
          }
        });
    };

    const setSelection = (tagName, idx) => {
      const html = monaco.codeEditor.getValue();
      const $elem = $(html)
        .find(tagName)
        .eq(idx);

      if (!$elem.length) {
        return;
      }

      const tagHtml = $elem.get(0).outerHTML;
      const htmlLines = html.split("\n");
      const tagHtmlLines = tagHtml.split("\n");
      let lineNumStart = 0;
      let lineNumEnd = 0;

      loop: for (let i1 in htmlLines) {
        const t11 = htmlLines[i1].trim();

        for (let i2 in tagHtmlLines) {
          const t21 = tagHtmlLines[i2].trim();

          if (t11 === t21) {
            lineNumStart = Number(i1) + 1;
            lineNumEnd = lineNumStart;

            for (let j = 1; j < tagHtmlLines.length; j += 1) {
              const t12 = htmlLines[Number(i1) + j]?.trim();
              const t22 = tagHtmlLines[Number(i2) + j]?.trim();

              if (t12 === t22) {
                if (j === tagHtmlLines.length - 1) {
                  lineNumEnd = Number(i1) + j + 1;
                  break loop;
                }
              } else {
                continue loop;
              }
            }
          }
        }
      }

      monaco.codeEditor.setSelection(new monaco.editor.Selection(lineNumStart, 0, lineNumEnd, 1000));
      monaco.codeEditor.revealLineInCenter(lineNumStart);
    };

    const init = (force) => {
      if (state.init && !force) {
        return;
      }

      loader.init().then((m) => {
        const codeEditor = m.editor.create(wrapperRef.value, {
          theme: "vs-dark",
          lineNumbers: "off",
          automaticLayout: true,
          renderIndentGuides: false,
          fixedOverflowWidgets: true,
          hover: {
            enabled: false,
          },
          minimap: {
            enabled: false,
          },
          padding: {
            top: 14,
          },
          // tabSize: 3,
        });

        codeEditor.onDidChangeModelContent(() => {
          const after = codeEditor.getValue();

          if (["css", "javascript"].includes(state.lang)) {
            const before = state.codes[state.lang];
            const comments = [];

            for (let b of before.split("\n")) {
              if (state.lang === "css" && b.startsWith("/* @") && b.endsWith(" */")) {
                comments.push(b);
              } else if (state.lang === "javascript" && b.startsWith("// @")) {
                comments.push(b);
              }
            }

            for (let a of after.split("\n")) {
              if (comments.includes(a)) {
                comments.splice(comments.indexOf(a), 1);
              }
            }

            if (comments.length) {
              const model = monaco.editor.editor.getModels()[0];

              if (!model) {
                return;
              }

              model.setValue(before);
              commLib.message.show("warning", "     .");
              return;
            }
          }

          state.codes[state.lang] = after;
        });

        codeEditor.addCommand(m.KeyMod.CtrlCmd | m.KeyCode.KEY_S, () => {
          pushCodes();
        });

        codeEditor.addCommand(m.KeyMod.Alt | m.KeyCode.KEY_C, () => {
          props.callback("keydown-alt", "c");
        });

        codeEditor.addCommand(m.KeyMod.Alt | m.KeyCode.KEY_P, () => {
          props.callback("keydown-alt", "p");
        });

        codeEditor.addCommand(m.KeyMod.Alt | m.KeyCode.KEY_R, () => {
          props.callback("keydown-alt", "r");
        });

        codeEditor.addCommand(m.KeyMod.Alt | m.KeyMod.Shift | m.KeyCode.KEY_1, () => {
          props.callback("keydown-alt-shift", "!");
        });

        codeEditor.addCommand(m.KeyMod.Alt | m.KeyMod.Shift | m.KeyCode.KEY_2, () => {
          props.callback("keydown-alt-shift", "@");
        });

        codeEditor.addCommand(m.KeyMod.Alt | m.KeyMod.Shift | m.KeyCode.KEY_3, () => {
          props.callback("keydown-alt-shift", "#");
        });

        codeEditor.addCommand(m.KeyMod.Alt | m.KeyCode.KEY_W, () => {});

        monaco.editor = m;
        monaco.codeEditor = codeEditor;
        setCodes();
      });

      state.init = true;
    };

    state.lang = editorLangs[0];

    onUnmounted(() => {
      monaco.editor?.editor.getModels().forEach((x) => x.dispose());
    });

    return {
      state,
      init,
      editorLangs,
      wrapperRef,
      setCodes,
      selectLang,
      pushCodes,
      setSelection,
    };
  },
};
</script>

<style lang="scss" scoped>
.component-editor {
  height: 100%;
  position: relative;
  overflow: hidden;

  > .wrapper {
    height: 100%;
    position: relative;
    overflow: hidden;
  }

  > .addon {
    position: absolute;
    left: 0;
    bottom: 0;
    background: #262626;

    > ul {
      list-style: none;
      padding: 0;
      margin: 0;

      > li {
        color: #aaa;
        cursor: pointer;
        font-size: 12px;
        position: relative;
        text-align: center;

        &.active {
          color: #fff;
          background: #1e1e1e;
        }
      }
    }

    > button {
      font-size: 14px;
      position: absolute;
      bottom: 0;
      right: 0;
      border-radius: 0;
    }
  }

  &[position="side"] {
    padding-bottom: 42px;
    padding-right: 10px;

    > .addon {
      width: 100%;
      height: 42px;

      > ul > li {
        float: left;
        padding: 12px 20px;
      }

      > button {
        height: 100%;
      }
    }
  }

  &[position="bottom"] {
    padding-left: 77px;

    > .addon {
      width: 77px;
      height: 100%;

      > ul > li {
        width: 100%;
        padding: 12px;
      }

      > button {
        width: 100%;
        height: 42px;
      }
    }
  }
}
</style>
