import axios from "axios";
import global from "../global";
import commLib from "./commonLib";

export default {
    get(url, parameters) {
        let args = {
            params: parameters
        };

        const token = commLib.cookie.get("token");
        args.headers = { "Authorization": `Bearer ${token}` };
        args.withCredentials = true;

        if (typeof parameters === "function") {
            console.warn("parameters is parameters");
        }

        return axios.get(global.site.url + url, args);
    },
    post(url, parameters) {
        let params = new URLSearchParams();
        const token = commLib.cookie.get("token");
        const config = {
            headers: { "Authorization": `Bearer ${token}` },
            withCredentials: true
        };

        if (typeof parameters === "function") {
            console.warn("parameters is parameters");
        }

        for (let i in parameters) {
            let val = parameters[i];

            if (typeof val === 'string')
                val = val.trim();

            params.append(i, val);
        }

        return axios.post(global.site.url + url, params, config);
    },
    delete: (url) => {
        const token = commLib.cookie.get("token");
        const config = {
            headers: { "Authorization": `Bearer ${token}` },
            withCredentials: true
        };

        return axios.delete(global.site.url + url, config);
    },
    postFile(url, formData) {
        const config = {
            headers: { 'Content-Type': 'multipart/form-data' },
            withCredentials: true
        };

        return axios.post(global.site.url + url, formData, config);
    }
};