import { createStore } from "vuex";

export default createStore({
    state: {
        account: {
            mid: null,
            login: false,
        },
        page: {
            name: null,
            seq: 1,
            maxSeq: 1,
            nid: "root",
        },
        refs: {
            console: null,
            webview: null
        },
        toggle: {
            tag: true,
            outline: true
        }
    },
    mutations: {
        setRefs(state, refs) {
            state.refs = refs;
        },
        setPage(state, page) {
            state.page = page;
        },
        setToggle(state, payload) {
            if (payload.tag !== undefined) {
                state.toggle.tag = payload.tag;
            }

            if (payload.outline !== undefined) {
                state.toggle.outline = payload.outline;
            }
        }
    }
})