<template>
  <div class="component-webview">
    <iframe ref="iframeRef" :src="initUrl" :class="{ 'dragging no-select': state.dragging }"></iframe>
    <div class="loading" v-if="!state.loaded">
      <Loading />
    </div>
  </div>
</template>

<script>
import { computed, onMounted, reactive, ref } from "vue";
import { useStore } from "vuex";
import global from "../global";
import Loading from "./Loading";

export default {
  props: {
    page: String,
    postback: Function,
  },
  components: { Loading },
  setup(props) {
    const store = useStore();
    const state = reactive({
      init: false,
      dragging: false,
      loaded: false,
      url: computed(() => `${global.site.url}/pages/${props.page}/${store.state.page.seq}`),
    });

    const initUrl = `${global.site.url}/pages/init`;
    const iframeRef = ref(null);

    const load = (seq, max) => {
      if (seq === store.state.page.seq) {
        reload();
      } else {
        store.commit("setPage", {
          ...store.state.page,
          seq: seq,
        });
      }

      if (max && seq !== store.state.page.maxSeq) {
        store.commit("setPage", {
          ...store.state.page,
          maxSeq: seq,
        });
      }
    };

    const reload = () => {
      state.loaded = false;
      postMessage("reload");
    };

    const postMessage = (type, value) => {
      iframeRef.value.contentWindow.postMessage(
        {
          type: type,
          value: value,
        },
        global.site.url
      );
    };

    window.addEventListener("message", (e) => {
      props.postback(e.data.type, e.data.value1, e.data.value2, e.data.value3, e.data.value4);
    });

    onMounted(() => {
      iframeRef.value.onload = () => {
        state.loaded = true;

        if (!state.init) {
          state.init = true;
          postMessage("location-replace", state.url);
        }
      };
    });

    return { state, initUrl, iframeRef, load, reload, postMessage };
  },
  watch: {
    "state.url": {
      handler() {
        this.postMessage("location-replace", this.state.url);
        this.state.loaded = false;
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.component-webview {
  height: 100%;
  position: relative;

  > iframe {
    width: 100%;
    height: 100%;
    border: 0;

    &.dragging {
      pointer-events: none;
    }
  }

  > .loading {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    text-align: center;
    margin-top: -100px;
  }
}
</style>
