<template>
  <div class="modal-login">
    <div class="pt-4 pb-4 text-center">
      <h1 class="font-parisienne color-point">Publessing</h1>
      <h3>Login with email</h3>
    </div>
    <div class="form mt-2">
      <div class="mb-4">
        <label for="email">Email</label>
        <input type="email" class="form-control" id="email" v-model="state.account.email" placeholder="name@publessing.io" autocomplete="off" @keypress.enter="login()" ref="emailRef" />
      </div>
      <div class="mb-5">
        <label for="password">Password</label>
        <input type="password" class="form-control" id="password" v-model="state.account.password" @keypress.enter="login()" />
      </div>
      <button class="btnbtn-lg btn-block btn-point no-radius" type="button" @click="login()">LOGIN</button>
    </div>
  </div>
</template>
<script>
import { onMounted, reactive, ref } from "vue";
import httpLib from "../libs/httpLib";
import commLib from "../libs/commonLib";

export default {
  props: {
    callback: Function,
  },
  setup(props) {
    const state = reactive({
      account: {
        email: "",
        password: "",
      },
    });

    const emailRef = ref(null);

    const login = () => {
      if (!state.account.password || !state.account.password.trim()) {
        document.getElementById("password").focus();
        return commLib.message.show("warning", " .");
      }

      httpLib
        .post("/api/account/login", state.account)
        .then((res) => {
          switch (res.status) {
            case 200:
              commLib.message.show("success", ".");
              props.callback("check-login");
              props.callback("close-modal");
              props.callback("go");
              break;

            default:
              commLib.message.show("danger", " .");
              break;
          }
        })
        .catch((err) => {
          switch (err.response?.status) {
            case 404:
              commLib.message.show("warning", "   .   .");
              break;

            case 429:
              commLib.message.show("warning", "  .");
              break;

            default:
              commLib.message.show("danger", " .");
              break;
          }
        });
    };

    onMounted(() => {
      setTimeout(() => {
        emailRef.value.focus();
      }, 250);
    });

    return {
      state,
      emailRef,
      login,
    };
  },
};
</script>
<style lang="scss" scoped>
.modal-login {
  padding: 25px;

  h1 {
    font-weight: bold;
    font-size: 50px;
  }

  h3 {
    margin-top: 25px;
  }

  input {
    height: 50px;
    border-radius: 0;

    &:focus {
      outline: 0;
      box-shadow: none;
      border-color: #146ece;
    }
  }

  button {
    font-size: 16px;
    height: 50px;
  }
}
</style>
