<template>
  <div class="component-home">
    <header>
      <div class="inner">
        <h1 class="font-parisienne color-point">Publessing</h1>
        <br />
        <h3 class="font-parisienne color-point">Draw the UI with commands</h3>
        <hr />
        <template v-if="login">
          <h2>Enter "<b>create startup</b>"</h2>
          <h2>then "<b>open startup</b>"</h2>
        </template>
        <template v-else>
          <h2>Enter "<b>join</b>" to create account</h2>
          <h2>or "<b>login</b>" to use the service</h2>
        </template>
        <button class="btn btn-point" @click="callback('open-console', !folded)" :title="`${folded ? 'Open' : 'Close'} Console (Alt + D)`">
          <span v-if="folded">OPEN </span>
          <span v-else>CLOSE </span>
          <span>CONSOLE</span>
        </button>
      </div>
    </header>
    <article>
      <div class="wrapper">
        <div class="title">Service Guide</div>
        <div class="content">
          <h1></h1>
          <div class="block">
            <h2> </h2>
            <ul>
              <li> <b class="pointer-u" @click="callback('open-console', false)">OPEN CONSOLE</b>  .</li>
            </ul>
          </div>
          <div class="block">
            <h2></h2>
            <ul>
              <li> <b>join</b>    .</li>
            </ul>
          </div>
          <div class="block">
            <h2></h2>
            <ul>
              <li> <b>login</b>    .</li>
            </ul>
          </div>
          <div class="block">
            <h2> </h2>
            <ul>
              <li> <b>create </b>    .</li>
            </ul>
          </div>
          <div class="block">
            <h2> </h2>
            <ul>
              <li> <b>open </b>    .</li>
            </ul>
          </div>
          <h1></h1>
          <div class="block">
            <h2> </h2>
            <table class="table table-bordered">
              <thead>
                <tr>
                  <th>Command</th>
                  <th>Description</th>
                  <th>Arguments</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(c, idx) in commands.console" :key="idx">
                  <td>{{ c.cmd }}</td>
                  <td>{{ c.desc }}</td>
                  <td>{{ c.args.join(" ") }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="block">
            <h2> </h2>
            <table class="table table-bordered">
              <thead>
                <tr>
                  <th>Command</th>
                  <th>Description</th>
                  <th>Arguments</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(c, idx) in commands.node" :key="idx">
                  <td>{{ c.cmd }}</td>
                  <td>{{ c.desc }}</td>
                  <td>{{ c.args.join(" ") }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </article>
    <footer>
      <div class="wrapper">
        <a class="brand">Publessing</a>
        <div class="copyright">&copy; 2021. Publessing. All rights reserved.</div>
      </div>
    </footer>
  </div>
</template>
<script>
import { computed } from "@vue/runtime-core";
import { useStore } from "vuex";
import global from "../global";

export default {
  props: {
    login: Boolean,
    callback: Function,
  },
  setup() {
    const store = useStore();
    const commands = {
      console: [],
      node: [],
    };
    const definedCommands = global.defines.commands;

    const folded = computed(() => {
      if (store.state.refs.console) {
        return store.state.refs.console.state.folded;
      }

      return false;
    });

    for (let i1 in definedCommands) {
      for (let i2 in definedCommands[i1]) {
        for (let c of definedCommands[i1][i2]) {
          commands[i1].push(c);
        }
      }
    }

    for (let i in commands) {
      commands[i].sort((a, b) => {
        if (a.cmd < b.cmd) {
          return -1;
        } else if (a.cmd > b.cmd) {
          return 1;
        }

        return 0;
      });
    }

    return { folded, commands };
  },
};
</script>
<style lang="scss" scoped>
.component-home {
  height: 100%;
  overflow: auto;

  .wrapper {
    width: 1140px;
    margin: 0 auto;
    max-width: 100%;
    padding: 0 15px;
  }

  > header {
    width: 100%;
    height: 100%;
    display: table;
    padding: 50px 0;
    position: relative;
    text-align: center;

    > .inner {
      display: table-cell;
      vertical-align: middle;

      > img {
        height: 100px;
        margin-bottom: 42px;
      }

      > h1 {
        font-size: 80px;
        font-weight: bold;
      }

      > h2 {
        font-size: 28px;
      }

      > h3 {
        font-weight: bold;
      }

      > hr {
        margin: 55px 0;
      }

      > button {
        margin-top: 50px;
        width: 300px;
        height: 60px;
        line-height: 60px;
        font-size: 18px;
        padding: 0;
        box-shadow: none;
        outline: 0;
        border-radius: 0;
      }
    }
  }

  > article {
    min-height: 100%;
    padding: 50px 0 25px 0;
    border-top: 1px solid #ddd;

    > .wrapper {
      > .title {
        font-size: 24px;
        font-weight: bold;
      }

      > .content {
        h1 {
          font-size: 18px;
          font-weight: 600;
          padding-bottom: 20px;
          margin-top: 35px;
          margin-bottom: 20px;
          border-bottom: 1px solid #ddd;
        }

        h2 {
          font-size: 16px;
        }

        .block {
          padding-left: 25px;
          margin-bottom: 15px;
          padding-top: 5px;
          padding-bottom: 5px;

          table {
            margin-top: 25px;

            th {
              background: #f3f7fb;
              border-bottom-width: 1px;
            }
          }
        }
      }
    }
  }

  > footer {
    border-top: 1px solid #ddd;
    padding: 30px 0;

    > .wrapper:after {
      content: "";
      clear: both;
      display: table;
    }

    .brand {
      float: left;
      color: #1259a7;
      cursor: pointer;
      font-family: "Parisienne", cursive;
      font-size: 42px;
      font-weight: bold;
      padding-right: 50px;
      text-decoration: none;
    }

    .copyright {
      padding: 17.5px 0;
      float: left;
    }
  }
}
</style>
