<template>
  <div class="component-command" ref="commandRef" :class="{ folded: folded, deactivated: !active }" :position="position">
    <div class="history" ref="historyRef" :class="{ none: !state.history.list.length }">
      <ul>
        <li :key="idx" v-for="(r, idx) in state.history.list">{{ r }}</li>
      </ul>
    </div>
    <div class="text" ref="textRef">
      <textarea placeholder="Enter a command. / Press Alt + C to focus." @keypress="keyPressInTextarea" @keydown="keyDownInTextarea" v-model="state.text" spellcheck="false" :disabled="!state.loaded" ref="textareaRef"></textarea>
    </div>
  </div>
</template>

<script>
import { nextTick, onMounted, reactive, ref } from "vue";
import { useStore } from "vuex";
import httpLib from "../libs/httpLib";
import commLib from "../libs/commonLib";
import global from "../global";

export default {
  props: {
    page: String,
    callback: Function,
    active: Boolean,
    folded: Boolean,
    position: String,
    login: Boolean,
  },
  setup(props) {
    const store = useStore();
    const state = reactive({
      init: false,
      text: "",
      loaded: true,
      history: {
        index: 0,
        list: [],
      },
      processes: [],
    });

    const defined = global.defines.commands;

    const consoleBeforeLoginCmds = defined.console.beforeLogin.map((x) => {
      return x.cmd;
    });

    const consoleBeforePageCmds = defined.console.beforePage.map((x) => {
      return x.cmd;
    });

    const consoleAfterPageCmds = defined.console.afterPage.map((x) => {
      return x.cmd;
    });

    const consoleWordDefines = [...defined.console.beforeLogin, ...defined.console.beforePage, ...defined.console.afterPage];
    const consoleWordDefinesCmds = [...consoleBeforeLoginCmds, ...consoleBeforePageCmds, ...consoleAfterPageCmds];

    const nodeWordDefines = [...defined.node.beforeLogin, ...defined.node.beforePage, ...defined.node.afterPage];
    const nodeWordDefinesCmds = nodeWordDefines.map((x) => {
      return x.cmd;
    });

    const commandRef = ref(null);
    const textareaRef = ref(null);
    const historyRef = ref(null);
    const textRef = ref(null);

    const runCommands = (command) => {
      const commandLines = commLib.getStrToArray(command, "\n", ";");

      for (let x of commandLines) {
        const cmd = x.trim();

        if (cmd) {
          state.processes.push(cmd);
        }
      }

      state.text = "";
      runCommand();
    };

    const appendLastHistory = (text) => {
      if (text && text.length) {
        state.history.list[state.history.list.length - 1] += `\n${text}\n`;
      }
    };

    const reloadCodes = (seq) => {
      if (store.state.refs.console.state.activeTab === "code") {
        store.state.refs.console.editorRef.setCodes(seq);
      }
    };

    const clearHistory = () => {
      state.history.list = [];
      state.history.index = 0;
      state.processes = [];
    };

    const selectComponents = (componentCategory) => {
      const consoleRef = store.state.refs.console;

      if (componentCategory) {
        for (let c of consoleRef.myComponentBoxRef.state.categories) {
          if (c === componentCategory) {
            consoleRef.selectTab("my-components", true);
            consoleRef.myComponentBoxRef.setCategory(c);
            return;
          }
        }

        for (let c of consoleRef.componentBoxRef.state.categories) {
          if (c === componentCategory) {
            consoleRef.selectTab("components", true);
            consoleRef.componentBoxRef.setCategory(c);
            return;
          }
        }
      }

      consoleRef.selectTab("components", true);
      consoleRef.componentBoxRef.setCategory(consoleRef.componentBoxRef.state.categories[0]);
      return;
    };

    const selectTemplates = (templateCategory) => {
      const consoleRef = store.state.refs.console;

      if (templateCategory) {
        for (let c of consoleRef.myTemplateBoxRef.state.categories) {
          if (c === templateCategory) {
            consoleRef.selectTab("my-templates", true);
            consoleRef.myTemplateBoxRef.setCategory(c);
            return;
          }
        }

        for (let c of consoleRef.templateBoxRef.state.categories) {
          if (c === templateCategory) {
            consoleRef.selectTab("templates", true);
            consoleRef.templateBoxRef.setCategory(c);
            return;
          }
        }
      }

      consoleRef.selectTab("templates", true);
      consoleRef.templateBoxRef.setCategory(consoleRef.templateBoxRef.state.categories[0]);
      return;
    };

    const runCommand = (seq, numbers) => {
      let command, commandWords, args;
      const consoleRef = store.state.refs.console;

      if (!seq) {
        seq = store.state.page.seq;

        if (!seq) {
          return;
        }
      }

      if (!state.processes.length) {
        if (!props.page) {
          return;
        }

        store.state.refs.webview.load(seq, true);
        reloadCodes(seq);
        return;
      }

      command = state.processes.splice(0, 1)[0].trim();
      command = commLib.getTextReplacedDoubleSpacesToSingle(command);
      commandWords = command.split(" ");

      state.history.list.push(command);
      state.history.index = state.history.list.length;

      if (!props.login && !consoleBeforeLoginCmds.includes(commandWords[0])) {
        state.processes = [];
        commLib.message.show("warning", "   .");
        return;
      }

      if (!props.page && ![...consoleBeforeLoginCmds, ...consoleBeforePageCmds].includes(commandWords[0])) {
        state.processes = [];
        commLib.message.show("warning", "   .");
        return;
      }

      if (commandWords[0].startsWith("@")) {
        if (commandWords.length < 2 || !nodeWordDefinesCmds.includes(commandWords[1])) {
          state.processes = [];
          commLib.message.show("warning", "   .");
          return;
        }
      } else if (!consoleWordDefinesCmds.includes(commandWords[0])) {
        state.processes = [];
        commLib.message.show("warning", "   .");
        return;
      }

      args = {
        page: props.page,
        seq: seq,
      };

      if (commandWords[0].startsWith("@")) {
        const cmd = nodeWordDefines.find((x) => {
          return x.cmd === commandWords[1];
        });

        if (commandWords.length < cmd.min || commandWords.length > cmd.max) {
          return commLib.message.show("warning", "    .");
        }

        switch (commandWords[1]) {
          case "append":
          case "prepend": {
            let num;

            if (!numbers) {
              numbers = commLib.cookie.get("numbers");

              if (numbers) {
                numbers = JSON.parse(numbers);
              } else {
                numbers = {};
              }
            }

            if (commandWords.length < 4) {
              if (commandWords.length === 2) {
                selectComponents();
              } else {
                selectComponents(commandWords[2].trim());
              }

              state.text = command.trim() + " ";
              return;
            }

            num = 1;

            if (numbers[commandWords[2]]) {
              num = numbers[commandWords[2]] + 1;
            }

            args.num = num;
            numbers[commandWords[2]] = num;
            break;
          }

          case "template":
            if (commandWords.length < 4) {
              if (commandWords.length === 2) {
                selectTemplates();
              } else {
                selectTemplates(commandWords[2].trim());
              }

              state.text = command.trim() + " ";
              return;
            }
            break;

          case "save":
            if (!commLib.isOnlyAlphabetString(commandWords[2])) {
              return commLib.message.show("warning", "   .");
            } else if (!commLib.isNumeric(commandWords[3])) {
              return commLib.message.show("warning", "   .");
            }

            store.state.refs.webview.postMessage("capture-node", {
              nid: commandWords[0].substring(1),
              category: commandWords[2].toLowerCase(),
              num: commandWords[3],
            });
            return;

          case "code": {
            let nid = "";
            let lang = "";

            if (commandWords.length > 1) {
              nid = commandWords[0].replace("@", "");

              if (commandWords.length > 2) {
                lang = commandWords[2];
              }
            }

            store.commit("setPage", {
              ...store.state.page,
              nid: nid ? nid : "root",
            });

            if (consoleRef.state.activeTab !== "code") {
              consoleRef.selectTab("code");
            }

            consoleRef.editorRef.setCodes();

            if (lang) {
              nextTick(() => {
                switch (lang.trim()) {
                  case "css":
                    consoleRef.editorRef.selectLang("css");
                    break;

                  case "js":
                  case "javascript":
                    consoleRef.editorRef.selectLang("javascript");
                    break;

                  default:
                    consoleRef.editorRef.selectLang("html");
                    break;
                }
              });
            }

            state.processes = [];
            return;
          }

          default:
            break;
        }
      } else if (commandWords[0].startsWith("#")) {
        return runCommand();
      } else {
        const cmd = consoleWordDefines.find((x) => {
          return x.cmd === commandWords[0];
        });

        if (commandWords.length < cmd.min || commandWords.length > cmd.max) {
          return commLib.message.show("warning", "    .");
        }

        switch (commandWords[0]) {
          case "vi": {
            const error = () => {
              commLib.message.show("warning", "    .");
            };

            httpLib
              .get(`/api/pages/details/${commandWords[1]}`)
              .then((res) => {
                if (res && res.data && typeof res.data === "object" && Object.keys(res.data).length) {
                  const obj = res.data;
                  obj.created = new Date(obj.created * 1000).toString();
                  obj.updated = new Date(obj.updated * 1000).toString();
                  appendLastHistory(JSON.stringify(obj, null, 4));
                } else {
                  error();
                }
              })
              .catch(() => {
                error();
              });

            state.processes = [];
            return;
          }

          case "join":
            if (store.state.account.login) {
              return commLib.message.show("warning", "  .");
            }

            props.callback("open-modal", "join");
            return;

          case "login":
            if (store.state.account.login) {
              return commLib.message.show("warning", "  .");
            }

            props.callback("open-modal", "login");
            return;

          case "logout":
            httpLib
              .post("/api/account/logout")
              .then(() => {
                clearHistory();
                commLib.message.show("success", ".");
                props.callback("check-login");
              })
              .catch(() => {
                commLib.message.show("error", " .");
              });
            return;

          case "rm": {
            if (commandWords[1] && commandWords[1].trim() && !confirm(`  ?`)) {
              return;
            }
            break;
          }

          case "exit": {
            clearHistory();
            props.callback("go");
            return;
          }

          case "export":
            commLib.postForm(`${global.site.url}/pages/export`, args);
            return;

          case "undo":
            if (store.state.page.seq > 1) {
              const seq = store.state.page.seq - 1;
              store.state.refs.webview.load(seq);
              reloadCodes(seq);
            }

            state.processes = [];
            return;

          case "redo":
            if (store.state.page.seq < store.state.page.maxSeq) {
              const seq = store.state.page.seq + 1;
              store.state.refs.webview.load(seq);
              reloadCodes(seq);
            }

            state.processes = [];
            return;

          case "reset": {
            if (!confirm(" ?")) {
              return;
            }
            break;
          }

          case "open":
            props.callback("go", commandWords[1].replace(".json", ""));
            return;

          case "cls":
          case "clear":
            clearHistory();
            return;

          default:
            break;
        }
      }

      args.cmd = command;
      state.loaded = false;

      httpLib
        .post("/api/commands/push", args)
        .then((res) => {
          state.loaded = true;

          switch (res.data) {
            case "pushed":
              if (commandWords[0].startsWith("@")) {
                switch (commandWords[1]) {
                  case "remove":
                    props.callback("set-nid", "root");
                    break;
                }
              }

              runCommand(seq + 1, numbers);
              break;

            case "created":
              commLib.message.show("success", "  .");
              props.callback("close-modal");
              break;

            case "reseted":
              store.state.refs.webview.load(1, true);
              store.state.refs.console.reloadComponents();
              consoleRef.state.activeTab = "command";
              commLib.message.show("success", " .");
              break;

            case "made":
              commLib.message.show("success", "  .");
              props.callback("close-modal");
              break;

            case "copied":
              commLib.message.show("success", "  .");
              break;

            case "renamed":
              commLib.message.show("success", "   .");
              props.callback("go");
              break;

            case "removed":
              commLib.message.show("success", "  .");
              props.callback("go");
              break;
          }

          switch (commandWords[0]) {
            case "ls": {
              let append = "";

              if (Array.isArray(res.data)) {
                append = res.data.join("\n");
              }

              appendLastHistory(append);
              break;
            }
          }

          if (!commandWords[0].startsWith("@")) {
            state.processes = [];
          }

          nextTick(() => {
            focus();
          });
        })
        .catch((err) => {
          state.loaded = true;
          state.processes = [];

          switch (err.response?.status) {
            case 400:
              commLib.message.show("warning", "   .");
              break;

            case 401:
              commLib.message.show("warning", " .");
              props.callback("open-modal", "login");
              return;

            case 403:
              commLib.message.show("warning", "   .");
              break;

            case 404:
              switch (err.response.data) {
                case "component":
                  commLib.message.show("warning", "   .");
                  break;

                case "node":
                  commLib.message.show("warning", "   .");
                  break;

                case "item":
                  commLib.message.show("warning", "   .");
                  break;

                case "page":
                  commLib.message.show("error", "  .");
                  break;

                default:
                  commLib.message.show("error", " .");
                  break;
              }
              break;

            case 409:
              commLib.message.show("warning", "    .");
              break;

            case 422:
              switch (err.response.data) {
                case "name":
                  commLib.message.show("warning", "    .");
                  break;

                case "wrong":
                  commLib.message.show("warning", "     .");
                  break;

                default:
                  commLib.message.show("error", " .");
                  break;
              }
              break;

            default:
              commLib.message.show("error", " .");
              break;
          }

          nextTick(() => {
            focus();
          });
        });
    };

    const keyPressInTextarea = (e) => {
      if (e.key === "Enter" && !e.shiftKey) {
        e.preventDefault();

        if (state.text) {
          runCommands(state.text.trim());
        } else {
          state.history.list.push("");
        }
      }
    };

    const keyDownInTextarea = (e) => {
      if (e.ctrlKey && e.key === "c" && !commLib.isTextSelected(textareaRef.value)) {
        state.text = "";
      } else if (e.key === "ArrowUp") {
        const arr = e.target.value.split("\n");
        const index = state.history.index - 1;

        if (arr[0].length < e.target.selectionStart) {
          return;
        } else if (state.history.index === 0) {
          return;
        }

        state.history.index = index;
        state.text = state.history.list[index];

        if (state.text.includes("\n")) {
          const arr = state.text.split("\n");
          state.text = arr[0];
        }

        e.preventDefault();
      } else if (e.key === "ArrowDown") {
        const value = e.target.value;
        const arr = value.split("\n");

        if (value.length - arr[arr.length - 1].length > e.target.selectionEnd) {
          return;
        }

        if (state.history.index >= state.history.list.length - 1) {
          state.history.index = state.history.list.length;
          state.text = "";
        } else {
          const index = state.history.index + 1;

          state.history.index = index;
          state.text = state.history.list[index];

          if (state.text.includes("\n")) {
            const arr = state.text.split("\n");
            state.text = arr[0];
          }

          e.preventDefault();
        }
      } else if (e.key === "Tab") {
        const texts = commLib.getTextReplacedDoubleSpacesToSingle(state.text.trim()).split(" ");
        e.preventDefault();

        if (!texts.length || !texts[0]) {
          return;
        }

        const runnableCommands = [];
        const key = state.text.startsWith("@") ? "node" : "console";
        const wordDefines = state.text.startsWith("@") ? nodeWordDefines : consoleWordDefines;
        const wordDefine = wordDefines.find((x) => {
          return x.cmd === texts[0];
        });

        if (state.text.startsWith("@") && texts.length > 1 && ["append", "prepend", "template"].includes(texts[texts.length - 2])) {
          const lastWord = texts[texts.length - 1];
          const consoleRef = store.state.refs.console;
          let categories = [];

          if (texts[texts.length - 2] === "template") {
            categories = [...consoleRef.templateBoxRef.state.categories];
          } else {
            categories = [...consoleRef.myComponentBoxRef.state.categories, ...consoleRef.componentBoxRef.state.categories];
          }

          for (let c of categories) {
            if (c.startsWith(lastWord)) {
              texts[texts.length - 1] = c;
              state.text = texts.join(" ") + " ";
              return;
            }
          }
        }

        if (!props.login) {
          runnableCommands.push(
            ...defined[key].beforeLogin.map((x) => {
              return x.cmd;
            })
          );
        } else if (!props.page) {
          runnableCommands.push(
            ...defined[key].beforePage.map((x) => {
              return x.cmd;
            }),
            ...defined[key].beforeLogin.map((x) => {
              return x.cmd;
            })
          );
        } else {
          runnableCommands.push(
            ...defined[key].afterPage.map((x) => {
              return x.cmd;
            }),
            ...defined[key].beforePage.map((x) => {
              return x.cmd;
            }),
            ...defined[key].beforeLogin.map((x) => {
              return x.cmd;
            })
          );
        }

        if (!wordDefine || (texts.length <= wordDefine.max && (key === "node" || wordDefine.node))) {
          const lastWord = texts[texts.length - 1];

          if (!lastWord.startsWith("@")) {
            for (let t of texts) {
              if (runnableCommands.includes(t)) {
                return;
              }
            }
          }

          for (let c of runnableCommands) {
            if (c.startsWith(lastWord)) {
              const wordDefine = wordDefines.find((x) => {
                return x.cmd === c;
              });

              texts[texts.length - 1] = c;
              state.text = texts.join(" ") + (wordDefine.space ? " " : "");
              return;
            }
          }
        }

        const consoleWordCms = consoleWordDefines
          .filter((x) => {
            return x.item;
          })
          .map((x) => {
            return x.cmd;
          })
          .filter((x) => {
            return state.text.startsWith(x);
          });

        if (consoleWordCms.length) {
          const texts = state.text.split(" ");

          if (texts.length < 2) {
            return;
          } else if (!runnableCommands.includes(texts[0])) {
            return;
          }

          const target = texts[1].trim();

          if (!target) {
            return;
          }

          httpLib.get("/api/commands/tab", { keyword: target }).then((res) => {
            if (res.data) {
              const cmd = texts[0];
              const wordDefine = consoleWordDefines.find((x) => {
                return x.cmd === cmd;
              });

              state.text = `${cmd} ${res.data}`;

              if (wordDefine && texts.length < wordDefine.min) {
                state.text += " ";
              }
            }
          });
          return;
        }

        if (!store.state.page.name) {
          return;
        }

        store.state.refs.webview.postMessage("tab-command", state.text);
        return;
      }
    };

    const focus = () => {
      textareaRef.value.focus();
    };

    const resize = () => {
      let result;
      const resultMinHeight = 37;

      historyRef.value.style.height = "auto";
      result = commandRef.value.offsetHeight - historyRef.value.getBoundingClientRect().height;

      if (result < resultMinHeight) {
        result = resultMinHeight;
        historyRef.value.style.height = commandRef.value.offsetHeight - resultMinHeight + "px";
      }

      textRef.value.style.height = result + "px";
      historyRef.value.scrollTop = historyRef.value.scrollHeight;
    };

    const adjust = () => {
      resize();
      focus();
    };

    const init = () => {
      if (state.init) {
        return;
      }

      adjust();
      state.init = true;

      /////// temp start ///////
      if (location.hash.replace("#/", "")) {
        let arr = [];
        // arr = [
        //   "reset",
        //   `

        //   @root append footer 100
        //   @root append footer 110
        //   @root append footer 200
        //   @root append footer 210
        //  `,
        // ];

        const tempRun = (commands) => {
          if (commands.length) {
            setTimeout(() => {
              state.text = commands.splice(0, 1)[0];
              keyPressInTextarea({ key: "Enter", preventDefault: function() {} });

              if (commands.length) {
                tempRun(commands);
              }
            }, 1000);
          }
        };

        tempRun(arr);
      }
      /////// temp end ///////
    };

    onMounted(() => {
      setTimeout(() => {
        init();
      }, 250);
    });

    return {
      store,
      state,
      init,
      commandRef,
      textareaRef,
      historyRef,
      textRef,
      adjust,
      focus,
      keyPressInTextarea,
      keyDownInTextarea,
      resize,
      runCommand,
      runCommands,
    };
  },
  watch: {
    "state.history.list": {
      handler() {
        nextTick(() => {
          this.resize();
        });
      },
      deep: true,
    },
  },
};
</script>

<style lang="scss" scoped>
.component-command {
  color: #fff;
  font-size: 14px;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;

  > .history {
    overflow: auto;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */

    > ul {
      list-style: none;
      padding: 16px 16px 0 15px;
      margin: 0;

      > li {
        min-height: 21px;
        white-space: pre;
        position: relative;
        padding-left: 14px;

        &:before {
          position: absolute;
          left: 0;
          top: 3px;
          font-size: 11px;
          content: ">";
          color: #fff;
        }
      }
    }

    &::-webkit-scrollbar {
      display: none;
    }

    &.none > ul {
      padding-top: 6px;
    }
  }

  > .text {
    padding: 8px 6px 6px 29px;
    position: relative;

    > textarea {
      width: 100%;
      height: 100%;
      border: 0;
      border-radius: 0;
      background: inherit;
      padding: 0;
      color: #fff;
      font-size: inherit;
      line-height: 1.76;
      resize: none;
      -ms-overflow-style: none; /* IE and Edge */
      scrollbar-width: none; /* Firefox */

      &:hover,
      &:focus {
        border: 0;
        outline: 0;
      }

      &::-webkit-scrollbar {
        display: none;
      }
    }

    &:before {
      position: absolute;
      left: 15px;
      top: 12px;
      font-size: 11px;
      content: ">";
      line-height: 1.7;
      color: #757575;
    }
  }

  &.deactivated {
    height: 41px;
    border-top: 1px solid #3c3c3c;
    position: absolute;
    top: auto;
    bottom: 0;
    width: 100%;
    padding-bottom: 0;
    z-index: 1;

    > .history {
      display: none;
    }

    > .text {
      height: 100% !important;
    }
  }

  &.folded {
    &[position="side"] {
      height: 100%;

      > .text {
        padding: 15px 2px;

        &:before {
          content: none;
        }
      }
    }
  }
}
</style>
